.circle-wrapper {
  width: 300px;
  height:300px;
  border-radius:51%;
  background:white;
  border:1px groove rgb(171, 166, 166);
  position:relative;
  margin:100px;
}

.circle {
  display:block;
  position:absolute;
  top:51%;
  left:51%;
  width:110px;
  height:110px;
  margin:-51px;
  background: white;
  border-radius:51%;
  text-align:center;
  line-height:100px;
  box-shadow: 3px 3px 3px grey;
  
}

.deg-0 {
  transform:translate(150px);
  line-height: 50px;
  word-break: break-all;
  word-wrap: break-word;
  color: rgb(209, 105, 67);
  
}



.deg-90 {
  transform:rotate(91deg) translate(150px) rotate(-91deg);
  line-height: 50px;
  word-break: break-all;
  word-wrap: break-word;
  color: rgb(232, 232, 83);
}



.deg-180 {
  transform:rotate(181deg) translate(150px) rotate(-181deg);
  line-height: 50px;
  word-break: break-all;
  word-wrap: break-word;
  color: rgb(86, 123, 210);
}



.deg-270 {
  transform:rotate(271deg) translate(150px) rotate(-271deg);
  line-height: 50px;
  word-break: break-all;
  word-wrap: break-word;
  color: rgb(39, 39, 169);
}


