.chatbot-container {
  display: flex;
  justify-content: flex-end;
  position: fixed;
  bottom: 0;
  right: 0;
  width: 25%;
  height: 50%;
  transition: transform 0.5s;
  z-index: 9999;
}

.chatbot {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.chatbot-header {
  padding: 10px;
  background-color: #f1f1f1;
  border-bottom: 1px solid #ddd;
}

.chatbot-body {
  padding: 10px;
  height: calc(100% - 120px);
  overflow-y: auto;
}

.chatbot-footer {
  padding: 10px;
  border-top: 1px solid #ddd;
}

.chatbot-input {
  width: 80%;
  padding: 5px;
}

.chatbot-send-btn {
  padding: 5px 10px;
}

.chatbot-close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 18px;
  background: none;
  border: none;
  cursor: pointer;
}
